import request from '@/utils/request'

/**
 * 查询当前人员活动报名列表
 * @param params
 */
export function getActivityList(params) {
    return request({
        url: '/popularScience/reservation/getActivityList',
        method: 'get',
        params
    })
}

/**
 * 查询活动剩余报名人数
 * @param params
 */
export function getActivityPeopleNumber(params) {
    return request({
        url: '/popularScience/reservation/getActivityPeopleNumber',
        method: 'get',
        params
    })
}

/**
 * 删除活动报名
 * @param params
 */
export function deleteActivityRecruit(uuids) {
    return request({
        url: '/popularScience/reservation/deleteActivityRecruit?uuids=' + uuids,
        method: 'delete',
    })
}

